const [t, setT] = useState(translations.en);


  function getCurrentPoolAddress() {
    return POOL_CONTRACTS[selectedToken]?.[network.chainId]?.[selectedPoolKey];
  }

  function splitDepositsForDisplay(deposits) {
    const recentDeposits = deposits.slice(-10); // We limit to 10 max
    const count = recentDeposits.length;

    if (count <= 5) {
      return {
        left: [...recentDeposits].reverse(), // most recent on top
        right: [],
      };
    }

    if (count < 10) {
      const extra = count - 5;
      const left = [...recentDeposits.slice(-extra)].reverse(); // the most recent
      const right = [...recentDeposits.slice(0, 5)].reverse(); // the oldest
      return { left, right };
    }

    // 10 deposits exactly or more
    return {
      left: [...recentDeposits.slice(-5)].reverse(),
      right: [...recentDeposits.slice(0, 5)].reverse(),
    };
  }

  function parseNote(note) {
    const regex =
      /^gothamcash-([a-zA-Z0-9]+)-(\d+(?:\.\d+)?)-(0x[0-9a-fA-F]{64})-(0x[0-9a-fA-F]{40})$/;
    const match = note.match(regex);
    if (!match) throw new Error("Invalid note format");
    return {
      token: match[1].toLowerCase(), // ex: "bnb", "USDC", "GOTHAM"
      denomination: match[2], // ex: "0.1", "10"
      secret: match[3], // ex: "0xabcdef...64 chars"
      contractAddress: ethers.getAddress(match[4]), // checksummed address
    };
  }

  function formatTimeAgo(timestamp: number): string {
    const seconds = Math.floor(Date.now() / 1000) - timestamp;

    if (seconds < 60) return `${seconds} ${seconds === 1 ? t.second : t.seconds}`;

    const minutes = Math.floor(seconds / 60);
    if (minutes < 60) return `${minutes} ${minutes === 1 ? t.minute : t.minutes}`;

    const hours = Math.floor(minutes / 60);
    if (hours < 24) return `${hours} ${hours === 1 ? t.hour : t.hours}`;

    const days = Math.floor(hours / 24);
    if (days < 30) return `${days} ${days === 1 ? t.day : t.days}`;

    const months = Math.floor(days / 30);
    if (months < 12) return `${months} ${months === 1 ? t.month : t.months}`;

    const years = Math.floor(months / 12);
    return `${years} ${years === 1 ? t.year : t.years}`;
  }

  const getSelectedAmountLabel = () => {
    const tokenData = TOKENS[selectedToken];
    const amountObj = tokenData.amounts.find(
      (a) => a.poolKey === selectedPoolKey
    );
    return amountObj ? amountObj.label : "";
  };

  function NetworkModal({ visible, onClose, selectedKey, onSelect }) {
    const [selectedNetworkKey, setSelectedNetworkKey] = useState(selectedKey);

    useEffect(() => {
      if (!visible) return;

      const handleKeyDown = (e) => {
        if (e.key === "Escape") {
          onClose();
        }
      };

      window.addEventListener("keydown", handleKeyDown);
      return () => window.removeEventListener("keydown", handleKeyDown);
    }, [visible, onClose]);

    if (!visible) return null;

    return (
      <div className="fixed inset-0 bg-black bg-opacity-90 flex items-center justify-center z-50">
        <div className="bg-white dark:bg-gray-900 border border-gray-800 rounded-lg p-6 w-80 relative">
          {/* Close button */}
          <button
            onClick={onClose}
            className="absolute top-7 right-10 text-gray-500 hover:text-gray-800 dark:hover:text-white font-semibold"
            aria-label="Close modal"
          >
            X
          </button>

          <h3 className="text-lg font-bold mb-4 text-gray-900 dark:text-white text-left">
            Select Network
          </h3>

          <div className="space-y-3">
            {Object.entries(NETWORKS)
              .filter(([_, net]) => net.enabled !== false)
              .map(([key, net]) => (
                <label
                  key={key}
                  className="flex justify-between items-center px-3 py-2 rounded hover:bg-gray-100 dark:hover:bg-gray-800 cursor-pointer text-gray-900 dark:text-white"
                  onClick={() => {
                    setSelectedNetworkKey(key);
                    onSelect(key);
                  }}
                >
                  <span>{net.name}</span>
                  <input
                    type="radio"
                    name="network"
                    value={key}
                    checked={selectedNetworkKey === key}
                    onChange={() => {}} // noop since handled in onClick
                    className={`
                  appearance-none h-5 w-5 rounded-full border-2 cursor-pointer
                  border-yellow-400 
                  checked:bg-yellow-400 checked:border-yellow-400
                  transition duration-150 ease-in-out
                `}
                  />
                </label>
              ))}
          </div>
        </div>
      </div>
    );
  }

  function handleNetworkSelect(selectedKey) {
    setShowNetworkModal(false);
    if (
      NETWORKS[selectedKey] &&
      selectedKey !==
        Object.keys(NETWORKS).find(
          (k) => NETWORKS[k].chainId === network.chainId
        )
    ) {
      switchNetwork(selectedKey);
    }
  }

  function disconnectWallet() {
    // Resetting the states linked to the wallet
    setProvider(null);
    setSigner(null);
    setContract(null);
    setAccount(null);
    setStatus("Wallet disconnected");
    setSecret("");
    setCommitment("");
    setCommitmentValid(null);
    setWithdrawAddress("");
  }

  function generateSecret() {
    const rand = ethers.hexlify(ethers.randomBytes(32));
    setSecret(rand);
    const com = ethers.keccak256(rand);
    setCommitment(com);
    setCommitmentValid(true);
    setStatus("Secret and commitment generated");
  }

  // Copy to clipboard function
  function copyToClipboard(text) {
    if (navigator.clipboard) {
      navigator.clipboard.writeText(text).then(
        () => {
          setStatus("Note copied to clipboard!");
        },
        () => {
          setStatus("Error while copying.");
        }
      );
    } else {
      setStatus("Clipboard API not supported");
    }
  }

  function getPoolAddress(token, chainId, poolKey) {
    const tokenContracts = POOL_CONTRACTS[token];
    if (!tokenContracts) throw new Error(`No contracts for token ${token}`);
    const poolAddressesForChain = tokenContracts[chainId];
    if (!poolAddressesForChain)
      throw new Error(
        `No pool contracts for chainId ${chainId} for token ${token}`
      );
    const address = poolAddressesForChain[poolKey];
    if (!address)
      throw new Error(
        `No pool contract for poolKey ${poolKey} on chainId ${chainId} for token ${token}`
      );
    return ethers.getAddress(address);
  }
  
  function formatGothamLabel(label) {
    if (!label.includes("GOTHAM")) return label; // Shortens only GOTHAM labels

    const [num, token] = label.split(" "); // ex: ["100000", "GOTHAM"]
    const value = parseInt(num, 10);

    if (value >= 1_000_000) return `${value / 1_000_000}M ${token}`;
    if (value >= 1_000) return `${value / 1_000}k ${token}`;
    return label;
  }

  const fetchDeposits = async (chainId, poolKey) => {
    if (!poolKey) {
      console.error("fetchDeposits called without poolKey");
      return;
    }

    const finalProvider = provider || getDefaultProvider(chainId);

    const networkInfo = await finalProvider.getNetwork();
    if (Number(networkInfo.chainId) !== chainId) {
      console.warn(
        "Aborted fetchDeposits: provider network does not match expected chainId"
      );
      return;
    }

    lastFetchedPoolKey.current = poolKey;

    try {
      const poolAddress = getPoolAddress(selectedToken, chainId, poolKey);
      const poolContract = new ethers.Contract(poolAddress, ABI, finalProvider);
      const allDeposits = await getAllDeposits(poolContract);

      if (lastFetchedPoolKey.current !== poolKey) return;

      setDeposits(allDeposits);

      const selectedAmounts = TOKENS[selectedToken].amounts;
      const selectedAmountObj = selectedAmounts.find(
        (a) => a.poolKey === poolKey
      );
      if (!selectedAmountObj) {
        console.error("Selected amount not found for poolKey:", poolKey);
        return;
      }

      const selectedAmountEther = Number(
        ethers.formatUnits(
          selectedAmountObj.value,
          TOKENS[selectedToken].decimals
        )
      );

      let numDeposits;

      if (selectedToken === "BNB") {
        const balance = await finalProvider.getBalance(poolAddress);
        numDeposits = Math.floor(
          Number(ethers.formatEther(balance)) /
            (selectedAmountEther - 0.01 * selectedAmountEther)
        );
      } else if (selectedToken === "USDC" || selectedToken === "GOTHAM") {
        const tokenAddress = TOKEN_ADDRESSES[network.chainId]?.[selectedToken];
        if (!tokenAddress) {
          console.error("Token address not found for chainId", chainId);
          return;
        }

        const erc20 = new ethers.Contract(
          tokenAddress,
          ["function balanceOf(address) view returns (uint256)"],
          finalProvider
        );
        const balance = await erc20.balanceOf(poolAddress);
        numDeposits = Math.floor(
          Number(ethers.formatUnits(balance, TOKENS[selectedToken].decimals)) /
            (selectedAmountEther - 0.01 * selectedAmountEther)
        );
      }

      if (lastFetchedPoolKey.current === poolKey) {
        setAnonymitySet(numDeposits);
      }
    } catch (err) {
      console.error("Error fetching deposits:", err);
    }
  };
  
  
  const fetchDepositsForAnalytics = async (chainId, token) => {
  if (!token || !chainId) return;

  const finalProvider = provider || getDefaultProvider(chainId);
  const newAnonymitySetByPool = {};

  for (const pool of TOKENS[token].amounts.filter(p => p && p.poolKey)) {
    const poolKey = pool.poolKey;

    try {
      const poolAddress = getPoolAddress(token, chainId, poolKey);
      // Conversion scurise en float
      const selectedAmountEther = parseFloat(ethers.formatUnits(pool.value, TOKENS[token].decimals));

      let numDeposits = 0;

      if (token === "BNB") {
        const balance = await finalProvider.getBalance(poolAddress);
        const balanceFloat = parseFloat(ethers.formatEther(balance));
        numDeposits = Math.floor(balanceFloat / (selectedAmountEther * 0.99)); // 0.01 discount
      } else if (token === "USDC" || token === "GOTHAM") {
        const tokenAddress = TOKEN_ADDRESSES[chainId]?.[token];
        if (!tokenAddress) {
          console.error("Token address not found for chainId", chainId, "and token", token);
          newAnonymitySetByPool[poolKey] = 0;
          continue;
        }

        const erc20 = new ethers.Contract(
          tokenAddress,
          ["function balanceOf(address) view returns (uint256)"],
          finalProvider
        );
        const balance = await erc20.balanceOf(poolAddress);
        const balanceFloat = parseFloat(ethers.formatUnits(balance, TOKENS[token].decimals));
        numDeposits = Math.floor(balanceFloat / (selectedAmountEther * 0.99));
      }

      newAnonymitySetByPool[poolKey] = numDeposits;
    } catch (err) {
      console.error("Error fetching deposits for analytics:", err);
      newAnonymitySetByPool[poolKey] = 0;
    }
  }

  setAnonymitySetByPool(newAnonymitySetByPool);
};



async function getNumDepositsForPool(chainId, token, pool) {
  try {
    const finalProvider = provider || getDefaultProvider(chainId);
    const poolKey = pool.poolKey;
    const poolAddress = getPoolAddress(token, chainId, poolKey);

    // selected amount in human units (float)
    const selectedAmount = parseFloat(
      ethers.formatUnits(pool.value, TOKENS[token].decimals)
    );

    if (token === "BNB") {
      const balance = await finalProvider.getBalance(poolAddress);
      const balanceFloat = parseFloat(ethers.formatEther(balance));
      // apply your 1% discount logic if needed
      return Math.floor(balanceFloat / (selectedAmount * 0.99));
    } else {
      const tokenAddress = TOKEN_ADDRESSES[chainId]?.[token];
      if (!tokenAddress) {
        console.error("Token address not found for chainId", chainId, "and token", token);
        return 0;
      }
      const erc20 = new ethers.Contract(
        tokenAddress,
        ["function balanceOf(address) view returns (uint256)"],
        finalProvider
      );
      const balance = await erc20.balanceOf(poolAddress);
      const balanceFloat = parseFloat(ethers.formatUnits(balance, TOKENS[token].decimals));
      return Math.floor(balanceFloat / (selectedAmount * 0.99));
    }
  } catch (err) {
    // si pool vide ou erreur -> 0
    console.debug("getNumDepositsForPool error", token, pool.poolKey, err);
    return 0;
  }
}




const fetchAllPoolsForAnalytics = async (chainId) => {
  if (!chainId) return;
  const newMap = {}; // { poolKey: numDeposits, ... }

  const tokens = Object.keys(TOKENS);

  // pour viter trop de requtes en mme temps, on peut squentiel par token ou limiter concurrence.
  for (const token of tokens) {
    const pools = TOKENS[token].amounts.filter(p => p && p.poolKey);
    // calcule les promesses pour chaque pool
    const promises = pools.map(pool => getNumDepositsForPool(chainId, token, pool));
    const results = await Promise.all(promises);

    // associe rsultats aux poolKeys
    pools.forEach((pool, idx) => {
      newMap[pool.poolKey] = results[idx] || 0;
    });
  }

  setAnonymitySetByPoolAll(newMap);
};




