// Graphical data calculation
useEffect(() => {
  const calculateTVLData = () => {
    const days = parseInt(analyticsTimeframe) || 7;
    const now = new Date();

    const result = { BNB: [], USDC: [], GOTHAM: [] };

    ["BNB", "USDC", "GOTHAM"].forEach(token => {
      // fetch history for this token
      const history = historicalTVL[token] || [];

      for (let i = days - 1; i >= 0; i--) {
        const day = new Date(now);
        day.setDate(now.getDate() - i);

        const dayStr = day.toLocaleDateString("en-US", { month: "short", day: "numeric" });

        // check if we have historical data for that day
        const entry = history.find(e => 
          e.day === day.getUTCDate() &&
          e.month === day.getUTCMonth() + 1 &&
          e.year === day.getUTCFullYear()
        );

        // if no historical data -> 0
        let tvl = entry?.tvl || 0;

        // if today -> use the real-time calculated value
        const isToday = day.getUTCDate() === now.getUTCDate() &&
                        day.getUTCMonth() === now.getUTCMonth() &&
                        day.getUTCFullYear() === now.getUTCFullYear();

        if (isToday) {
          tvl = tokenTVLsUSD[token] || tvl;
        }

        result[token].push({ day: dayStr, tvl: parseFloat(tvl.toFixed(2)) });
      }
    });

    setTvlData(result);
  };

  calculateTVLData();
}, [historicalTVL, tokenTVLsUSD, analyticsTimeframe]);



// ---------------------------
// 1 USD TVL calculation per token
// ---------------------------
useEffect(() => {
  if (!tokenPricesUSD || Object.keys(anonymitySetByPool).length === 0) return;

  const newTokenTVLsUSD = {};

  Object.keys(TOKENS).forEach(token => {
    const price = tokenPricesUSD[token] || (token === "USDC" ? 1 : 0);

    const tokenTVL = TOKENS[token].amounts
      .filter(pool => pool && pool.poolKey)
      .reduce((sum, pool) => {
        const numDeposits = anonymitySetByPool[pool.poolKey] || 0;
        const poolValue = parseFloat(
          ethers.formatUnits(pool.value, TOKENS[token].decimals)
        );
        return sum + numDeposits * poolValue * price;
      }, 0);

    newTokenTVLsUSD[token] = parseFloat(tokenTVL.toFixed(2));
  });

  setTokenTVLsUSD(newTokenTVLsUSD);
}, [tokenPricesUSD, anonymitySetByPool]);


