useEffect(() => {
  if (!tokenPricesUSD || Object.keys(anonymitySetByPoolAll).length === 0) {
    setTotalGlobalTVLUSD(0);
    return;
  }

  let total = 0;
  Object.keys(TOKENS).forEach(token => {
    const price = tokenPricesUSD[token] || (token === "USDC" ? 1 : 0);
    TOKENS[token].amounts
      .filter(pool => pool && pool.poolKey)
      .forEach(pool => {
        const numDeposits = anonymitySetByPoolAll[pool.poolKey] || 0;
        const poolValue = pool?.value ? parseFloat(ethers.formatUnits(pool.value, TOKENS[token].decimals)) : 0;
        total += numDeposits * poolValue * price;
      });
  });

  setTotalGlobalTVLUSD(parseFloat(total.toFixed(2)));
}, [anonymitySetByPoolAll, tokenPricesUSD]);









